import Card from "./card";

export default function Helper() {
  return (
    <section className="section helper-section valign-wrapper">
      <div className="container">
        <div className="row">
          <div className="col s12 m12 l4">
            <Card
              title="Descubra"
              content={
                <div>
                  <h5>
                    Desvende novos mundos entre as pginas. Deixe-se envolver
                    pelas sugestes personalizadas do Book N' Books e descubra
                    obras que desafiem, encantem e inspirem.
                  </h5>
                  <hr />
                  <h6>
                    Busque ttulos literrios utilizando a barra de pesquisa no
                    incio da pgina!
                  </h6>
                </div>
              }
            />
          </div>
          <div className="col s12 m12 l4">
            <Card
              title="Leia"
              content={
                <h5>
                  Explore a magia das palavras. Com o Book N' Books, sua jornada
                  literria ganha vida. De clssicos atemporais a lanamentos
                  emocionantes, embarque em histrias que enriquecem a mente e
                  alimentam a imaginao.
                </h5>
              }
            />
          </div>
          <div className="col s12 m12 l4">
            <Card
              title="Compartilhe"
              content={
                <h5>
                  Encontre sua tribo literria. Compartilhe suas descobertas,
                  opinies e recomendaes com a comunidade Book N' Books.
                  Juntos, criamos um espao onde a paixo pela leitura se torna
                  uma experincia coletiva.
                </h5>
              }
            />
          </div>
        </div>
      </div>
    </section>
  );
}
